@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");

:root {
  --header-height: 3rem;
  --font-semi: 600;
}

/* ====color====*/
:root {
  --first-color: #4070f4;
  --second-color: #0e2431;
}

/* ==== */

:root {
  --body-font: "Poppins", san-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}

@media screen and( min-width: 768px) {
  :root {
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
  }
}

/*===margin===*/

:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*==== z-index ====*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*==== BASE ====*/
*,
::before,
::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--second-color);
}

h1,
h2,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}

.section-title {
  position: relative;
  font-size: var(--h2-font-size);
  color: var(--first-color);
  margin-top: var(--mb-2);
  margin-bottom: var(--mb-4);
  text-align: center;
}

.section-title::after {
  position: absolute;
  content: "";
  width: 64px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  background-color: var(--first-color);
}

.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
}

/* ==== Layout === */
.body-grid {
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-right: var(--mb-2);
  margin-left: var(--mb-2);
}

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: white;
  box-shadow: 0 1px 4px rgba(146, 161, 176, 0.15);
}

/* nav */
.nav {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: var(--font-semi);
}

@media screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: var(--header-height);
    right: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: var(--second-color);
    transition: 0.5s;
  }
}

.nav__item {
  margin-bottom: var(--mb-4);
}

.nav__link {
  position: relative;
  color: white;
}

.nav__link:hover::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}

.nav__logo {
  color: var(--second-color);
}

.nav__toggle {
  color: var(--second-color);
  font-size: 1.5rem;
  cursor: pointer;
}

/* active-menu  */
.active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}

.show {
  right: 0;
}

.home {
  height: calc(100vh - 3rem);
  row-gap: 1rem;
}

.home__data {
  align-self: center;
}

.home__title {
  font-size: var(--big-font-size);
  margin-bottom: var(--mb-5);
}

.home__title-color {
  color: var(--first-color);
}

.home__social {
  display: flex;
  flex-direction: column;
}
.home__social-icon {
  width: max-content;
  margin-bottom: var(--mb-2);
  margin-left: var(--mb-1);
  font-size: 2.5rem;
  color: var(--second-color);
}

.home__social-icon:hover {
  color: var(--first-color);
}
.home__image {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 295px;
  border-radius: 999px;
}

.button {
  display: inline-block;
  background-color: var(--first-color);
  color: white;
  padding: 0.75rem 2.5rem;
  font-weight: var(--font-semi);
  border-radius: 0.5rem;
  margin-left: var(--mb-2);
}

.button:hover {
  box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
}

.about__container {
  row-gap: 2rem;
  text-align: center;
}

.about__subtitle {
  margin-bottom: var(--mb-2);
}

.about__img {
    display: flex;
    justify-content: center;
}

.about__img img {
  width: 200px;
  border-radius: 0.5rem;
}

/* SKILLS */

.skills__container{
  row-gap: 2;
  text-align: center;
  padding: 0 1rem;
}

.skills__subtitle{
  margin-bottom: var(--mb-2);
}

.skills__text{
  margin-bottom: var(--mb-4);
}

.skills__data{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: var(--font-semi);
  padding: 0.5rem 1rem;
  margin-bottom: var(--mb-4);
  border-radius: 0.5rem;
  box-shadow: 0 4px 25px rgba(14,36, 49, 0.15) ;
}

.skills__icon{
  font-size: 2rem;
  margin-right: var(--mb-2);
  color: var(--first-color)
}

.skills__names{
  display: flex;
  align-items: center;
}

.skills__bar{
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: var(--first-color);
  height: 0.25rem;
  border-radius: 0.5rem;
  z-index: var(--z-back);
}

.skills__react{
  width: 80%;
}
.skills__angular{
  width: 60%;
}
.skills__node{
  width: 85%;
}
.skills__spring{
  width: 55%;
}

.skills__image{
  border-radius: 0.5rem;
}

.work{
  text-align: center;
}

.work__container{
  row-gap: 2;
  padding:  0 1rem;
}

.work__img{
  box-shadow: 0 4px 25px rgba(14,36, 49, 0.15);
  border-radius: 0.5rem;
  overflow: hidden;
  margin-bottom: var(--mb-2);
}

.work__img img{
  transition: 1s;
  cursor: pointer;
}

.work__img img:hover{
  transform: scale(1.1);
}

.contact__input{
  width: 100%;
  font-size: var(--normal-font-size);
  font-weight: var(--font-semi);
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1.5px solid var(--second-color);
  outline: none;
  margin-bottom: var(--mb-4) ;
}

.contact__button{
  display: block;
  border: none;
  outline: none;
  font-size: var(--normal-font-size);
  cursor: pointer;
  margin-left: auto;
}

.footer{
  background-color: var(--second-color);
  color: white;
  text-align: center;
  font-weight: var(--font-semi);
  padding: 2rem 0;
}

.footer__title{
  font-size: 2rem;
  margin-bottom: var(--mb-4) ;
}

.footer__social{
  margin-bottom: var(--mb-4);
}

.footer__icon{
  font-size: 1.5rem;
  color: white;
  margin: 0 var(--mb-2);
}

@media screen and (min-width: 768px){
  body{
    margin: 0;
  }
  .section{
    padding-top: 4rem;
    padding-bottom: 3rem;
  }

  .section-title::after{
    width: 80px;
    top: 3rem;
  }
  .nav{
    height: calc(var(--header-height) + 1rem);
  }

  .nav__list{
    display: flex;
    padding-top: 0;

  }
  .nav__item{
    margin-left: var(--mb-6);
    margin-bottom: 0;
  }
  .nav__toggle{
    display: none;
  }

  .nav__link{
    color: var(--second-color)
  }
  .home{
    height: 80vh;
  }
  .home__data{
    align-self: flex-end;
    padding-left: 2rem;
  }
  .home__title{
    margin-top: calc(var(--header-height) + 1rem );
    font-size: 70px;
  }
  .home__social{
    padding-top: 0;
    padding-bottom: 2.5rem;
    flex-direction: row;
    align-self: flex-end;
    padding-left: 2rem;
  }
  .home__social-icon{
    margin-bottom: 0;
    margin-right: var(--mb-4);
  }
  .home__image{
    width: 457px;
    top: 10%;
    object-fit: contain;
  }

  .about__container, .skills__container{
    /* margin-top: 1rem; */
    display: grid;
    grid-template-columns: repeat(2,1fr);
    align-items: center;
    text-align: initial;
  }
  .about__img img{
    width: 300px;
  }

  .skills__image{
    width: 500px;
    margin-left: 5rem;
  }
  .work__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(2,2fr);
    column-gap: 2rem;
  }

  .contact__form{
    width: 360px;
  }
  .contact__container{
    justify-items: center;
  }


}